@import "../../styles/variables.scss";

.search-section {
  position: relative;
  display: flex;
  flex-direction: column;

  @media (min-width: 1024px) {
    flex-basis: 30%;
    flex-shrink: 0;
  }
}

.search-section__title {
  padding: 10px;
}

.search-section__form {
  background-color: $basic-bgc-color;
}

.search-section__relative {
  position: relative;
  display: flex;
  width: 90%;
  margin: 0 auto;

  @media (orientation: landscape) {
    width: 65vw;
  }

  @media (min-width: 411px) and (orientation: portrait) {
    width: 80%;
  }

  @media (min-width: 768px) and (orientation: portrait) {
    width: 55%;
  }

  @media (min-width: 1024px) {
    width: 80%;
  }
}

.search-section__results-position {
  width: 100%;
  height: 100%;
  position: relative;
  flex-grow: 1;
}
