.loading-page {
  position: fixed;
  z-index: 99;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(255, 255, 255, 0.808);
}

.loading-page__name {
  text-transform: uppercase;
  font-size: 30px;
}
